.App {
	text-align: center;
	&-header {
		background-color: #282c34;
		height: 100px; } }

.header {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
	margin-bottom: 10px; }

.h3 {
	margin-bottom: 5px; }

.h4 {
	margin-top: 10px; }

span {
	color: red;
 }	// font-size: 12px

.messagebox {
	border: 2px solid blue;
	width: 400px;
	height: 50vh;
	overflow-y: scroll;
	margin: 0 auto;
	box-sizing: border-box;
	text-align: right;
	padding: 15px;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
 }	// scroll-snap-type: x mandatory
